<template>
    <div>
        <div class="login_heard">
            <div>
                <van-icon name="arrow-left" color="#fff"  @click="go"/>
            </div>
            <div><p class="p">
                修改密码</p></div>
            <div></div>
        </div>
        <van-form @submit="onSubmit">
            <van-field
                v-model="oldpassword"
                name="oldpassword"
                type="password"
                label="旧密码"
                colon
                placeholder="旧密码"
                :rules="[{ required: true, message: '请填写旧密码' }]"
            />
            <van-field
                v-model="newpassword"
                type="password"
                name="newpassword"
                colon
                label="新密码"
                placeholder="新密码"
                :rules="[{ required: true, message: '请填写新密码' }]"
            />
            <div style="margin: 16px;">
               <van-button round block type="info" native-type="submit"  color="rgb(16, 173, 81)">保存</van-button>
            </div>
        </van-form>
        
    </div>
    
  
</template>

<script>
import { Icon,Button,Form,Field ,Toast   } from 'vant';
export default {
    name:'mychangemima',
    data() {
        return {
            newpassword: '',
            oldpassword: '',
            password:''
        }
    },
    components:{
        [Icon.name]:Icon,
        [Button.name]:Button,
        [Form.name]:Form,
        [Field.name]:Field,
        [Toast.name]:Toast  
    },
    methods:{
        onSubmit(values) {
            console.log(values);
            this.$axios({
                method:'post',
                url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/updatePwd',
                data:{
                    'oldPassword':values.oldpassword,
                    'newPassword':values.newpassword,
                    'uid':localStorage.getItem('uid')
                }
            }).then((res)=>{
                console.log(res);
                Toast(res.data.msg)
                if(res.data.code==0){
                    this.$router.push('/login')
                }
                
            })


        },
        go(){
            this.$router.go(-1);
        }
    }
}
</script>

<style lang="less" scoped>
.login_heard{
    display: flex;
    height: 45px;
    background-color: rgb(16, 173, 81);
    // line-height: 45px;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    .p{
        color: #fff;
        text-align: center;
    }
}
.phone{
    display: flex;
    height: 50px;
    border-bottom: 1px solid #bab7b7;
    align-items: center;
    padding: 0 10px;
    .icon{
        width: 20px;
        height: 20px;
        
    }
    input{
        flex: 3;
        border: none;
        margin: 0 5px;
    }
   
    button{
        border: none;
        border-radius: 10px;
        padding: 3px;
    }
   
}
.btn{
    width: 350px;
    margin: 25px auto;
    // text-align: center;
}
</style>